    
import '../style2.css'
import { useState } from 'react'
import { motion } from 'framer-motion'
export default function Inicio () {
    const [clicked,setClicked] = useState(true)
    const handleClick = ()=>
    {
        setClicked(!clicked)
      
    }
    const inactive = {opacity: 0,scale:0}
    const active = {opacity: 1,scale:1}

    
    return(
        <div className="articles example">
            
 <h1 id='inicio'><i>INICIO</i></h1><p>
 Para comenzar, se tomar la <b>Tarjeta de Inicio.</b>
<br/>
La misma contiene la historia central del juego.
<br/>
Puede leerse o reproducir el audio ( a travs de escanear el
cdigo QR) con su <i animate={clicked ? active : inactive} style={{textDecoration:'underline', color: '#25418C'}}  onClick={handleClick}>narracin.</i>
<br/>

<br/>



<motion.p animate={clicked ? inactive : active} className={clicked ? 'inactive' : ''}> Es viernes por la tarde, los chicos y las chicas de 1, 2, 3 y 4 ao son los nicos cursos que
quedan en la escuela. Estn a punto de finalizar su da de clase, guardando las carpetas en sus
mochilas, cuando algo misterioso sucede: todas las ventanas y puertas de la escuela se cierran
de golpe. Docentes y directivos desaparecen. Solo las y los estudiantes quedan adentro. Nadie
puede salir. No hay forma de comunicarse con el exterior. De repente una voz se hace
escuchar. Anuncia que la nica forma de poder salir de la escuela y no quedarse all todo el fin
de semana es encontrando la contrasea de 4 dgitos que destrabar todas las salidas.
<br/>
</motion.p>
Una vez que se ha ledo el texto, se explica la dinmica a los
participantes 
</p>
        </div>
    )
        
    
}